import Image from 'next/image'
import Link from 'next/link'
import { notFound } from 'next/navigation'


type Props = { params: { slug: string } }

export function generateStaticParams() {
  return projectInfo().map((p) => ({ slug: p.params }))
}

export function generateMetadata({ params }: { params?: { slug?: string } } = {}) {
  if (!params?.slug) return {}
  const project = projectInfo().find((p) => p.params === params.slug)
  if (!project) return {}
  return { title: project.title, description: project.description }
}

export default function ProjectPage({ params }: Props) {
  const project = projectInfo().find((p) => p.params === params.slug)
  if (!project) return notFound()


  return (
    <main className="mx-auto max-w-4xl px-4 py-12">
      <Link href="/projects" className="text-sm text-gray-400 mb-4 inline-block">
        ‚Üê Back
      </Link>

      <h1 className="text-4xl font-bold text-lightGray">{project.title}</h1>
      <p className="mt-2 text-sm text-lightGray">{project.year}</p>

      <div className="mt-6 relative w-full aspect-[16/9] rounded-lg overflow-hidden">
        <Image src={project.image} alt={project.title} fill className="object-cover" />
      </div>

      <div className="mt-6 p-4 rounded-[12px]" style={{ background: 'rgba(159, 184, 173, 0.06)' }}>
        <p className="text-lg text-lightGray">{project.description}</p>

        <div className="mt-4 flex gap-4">
          {project.demo && (
            <a href={project.demo} target="_blank" rel="noreferrer" className="text-sm text-[lightGray] underline">
              Live demo
            </a>
          )}
          {project.code && (
            <a href={project.code} target="_blank" rel="noreferrer" className="text-sm text-[lightGray] underline">
              Source code
            </a>
          )}
        </div>
      </div>

      {gallery.length > 0 && (
        <div className="mt-6 grid grid-cols-1 gap-4 md:grid-cols-3">
          {gallery.map((g, i) => {
            const url = getImageUrl(g)
            return (
              <div key={i} className="relative w-full aspect-[4/3] overflow-hidden rounded-md">
                {url ? (
                  <img src={url} alt={`${project.title} ${i + 2}`} className="h-full w-full object-cover" />
                ) : null}
              </div>
            )
          })}
        </div>
      )}
    </main>
  )
}

